<template>
	<div class="flex w-full">
		<div class=" mx-auto self-center">
			<a href="http://www.ejoya.co/">
				<img src="@/assets/images/logo/logo.png" alt="login" class="mx-auto form-logo">
			</a>

			<vx-card class="no-box-shadow card-down">
				<div slot="no-body" class="">
					<div class="vx-row">
						<div class="vx-col sm:w-full md:w-full lg:w-1/2 mx-auto self-center bg-white">
							<div class="p-8">
								<div class="vx-card__title mb-8">
									<h1 class="mb-4 font-bold">Create an Account</h1>
									<p class="font-light">Create an Account to get started.</p>
								</div>

								<form @submit.prevent="signup">

										<vs-input label="What is your Artist Name" placeholder="Enter your Artist Name"
										v-model="artistName" type="text" class="w-full mb-6" />
	
									<vs-input label="What is your Email Address" placeholder="Enter your Email Address"
										type="email" v-model="emailAddress" class="w-full mb-6" />

										<vs-input label="What is your Mobile Number" placeholder="Enter your Mobile Number"
										type="text" v-model="phoneNumber" maxlength="12" class="w-full mb-6" />
	
									<vs-input type="password" label="Enter Password" placeholder="Password" v-model="password"
										class="w-full mb-4" />
	
									<vs-input type="password" label="Confirm Password" placeholder="Password"
										v-model="confirmPassword" class="w-full mb-4" />
	
									<div class="gradient-btn-div">

										
										<vs-button class="gradient-btnn" :color="themeOne"
											:gradient-color-secondary="themeTwo" type="gradient">Get Started</vs-button>
									</div>

									<div class="mt-3 text-center">
											<router-link class="text-black my-2" to="/login" color="dark" type="flat">Already have an Account? Log in
												here!</router-link>
										</div>
	

								</form>

							</div>
						</div>
						<div class="vx-col hidden sm:hidden md:hidden lg:block lg:w-1/2 mx-auto self-center">
							<img src="@/assets/images/ejoya/ejoya-login.png" alt="login" class="mx-auto login-img">
						</div>
					</div>
				</div>
			</vx-card>
		</div>
	</div>
</template>

<script>
	import http from '../../http';
	export default {
		data() {
			return {
				title: 'Ejoya Music',
				themeOne: '#E5396B',
				themeTwo: '#C85B80',
				artistName: '',
				emailAddress: '',
				password: '',
				phoneNumber: '',
				confirmPassword: '',
				checkBox1: false
			}
		},
		methods: {
			signup(){

				let axiosConfig = {
					headers: {
					}
				};
				
				if (this.validateInput()) {
					this.$store.dispatch("SET_LOADING", true);

						http
							.post("/signup/", {
								"email": this.emailAddress,
								"fullname": this.artistName,
								"password": this.password,
								"phonenumber": this.phoneNumber,
							}, axiosConfig)
							.then(response => {
								this.$store.dispatch("SET_LOADING", false);
								this.handleLogin(response);
								// this.handleLogin(response);
							})
							.catch(error => {
								this.$store.dispatch("SET_LOADING", false);
								console.log(error);
								this.handleError(error);
							});
					}	
			},
			validateInput(){

				let artistName = this.artistName;
				let email = this.emailAddress;
				let phone = this.phoneNumber;
				let password1 = this.password;
				let password2 = this.confirmPassword;

				// If password not entered 
                if (password1 == '') 
					this.$swal(this.title, "Please Enter Password !", "info");
                      
                // If confirm password not entered 
                else if (password2 == '') 
					this.$swal(this.title, "Please Enter Confirm Password !", "info");
                      
                // If Not same return False.     
                else if (password1 != password2) { 
					this.$swal(this.title, "Password did not match: Please try again...", "info");
                    return false; 
				} 
				else if(artistName == ''){
					this.$swal(this.title, "Please Enter Artist Name !", "info");
					return false;
				}
				else if(email == ''){
					this.$swal(this.title, "Please Enter Email Address !", "info");
					return false;
				}
				else if(phone == ''){
					this.$swal(this.title, "Please Enter Mobile Number !", "info");
					return false;
				}
  
                // If same return True. 
                else{ 
                    return true; 
                } 
			},
			handleLogin(response) {
                if (response.status == 201) {
                    this.$swal(this.title, "Account Created Successfully!", "success");
					let data = response.data.data;
					
                    // this.saveUserToStorage(data);
                    // this.$store.dispatch("SET_TOKEN", data.token);
                    // this.$store.dispatch("SET_USER", data.user);

                    setTimeout(() => {
                        this.$router.push("/login");
                    }, 1000);
                }
			},
			handleError(error){
				if(error.response.status == 400){
					this.$swal(this.title, "User with this email already exists.", "danger");
				}
			}
		}
	}
</script>